.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* padding: 20px; */
  box-sizing: border-box;
  background: linear-gradient(#060a0c,#061016);
  height: 100vh;
}
.header{
  font-size: 30px;
  font-family: "Gochi Hand", cursive;
  font-weight: 400;
  font-style: normal;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  width: 100vw;
  background-color: #2c2c2c2a;
  height: 100px;
}
.sidecontainer {
  height: calc(100vh - 100px);
  max-width: 1500px;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.overlay {
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  flex-direction: column;
  position: absolute;
  background: linear-gradient(#060a0c09,#061016);
  top: 0;
  left: 0;
  opacity: 1;
  height: 100vh;
  width: 100%;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: visible;
}

.overlayHidden {
  opacity: 0;
  transition: opacity 0.3s ease-in-out; 
  visibility: hidden;
  transition-delay: 0.3s; 
  pointer-events: none;
}




.timerText {
  text-shadow: #060a0c;
  color: white;
  font-size: 100px;
  font-weight: bold;
}

.middbox {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-bottom: 20px;
}

.textOverlay {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #091216b0;

}

.cards {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 17vmin;
  max-width: 200px;
  height: 20vmin;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  background-color: #0e1518;
  border: 2px solid #dddddd38;
  transition: 0.3s ease-in-out;
  background-blend-mode: lighten;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.cards:disabled {
  cursor: not-allowed;
}

.cards:hover {
  border: 2px solid #167bf0ad;
  box-shadow: 0 0 50px #0db8ec70;
  transform: scale(1.05);
}
@media (max-width: 1300px) {
  .cards {
    max-width: 115px;
    max-height: 125px;
  }
}

.image {
  overflow: hidden;
  height: 100%;
  width: 100%;
  background-color: #1e88e5;

  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.counterbox {
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  color: white;
  font-style: normal;
  font-family: "Itim", cursive;
  /* padding: 5px; */
  justify-content: center;
  align-items: center;
  /* margin-top: 10px; */
  font-size: 30px;
  border-radius: 5px;
}
.counterText{
  font-style: normal;
  justify-content: center;
  align-items: center;
  display: flex;
  margin-right: 10px;
  color: white;
  font-weight: bold;
}
.counterText2{
  background-color: #116db8d2;
  font-weight: 400;
  font-style: normal;
  width: 30%;

  justify-content: center;
  align-items: center;
  display: flex;
  color: white;
  font-size: 25px;
  font-weight: bold;
}


.resetButton {
  font-size: 19px;
  color: white;
  padding: 10px 20px;
  height: 50px;
  border: 1px solid rgba(255, 255, 255, 0.075);
  background-color: #116db818;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s;
}

.resetButton:hover {
  background-color: #2c9dffe0;
  box-shadow: 0 0 50px #1e88e56c;
}

.modalOverlay {

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.849);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.gradeText {
  color: white;
  width: 100%;
  /* padding: 50px 0px; */
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif ;
  background-color: #0a0a0a3d;
  text-align: center;
  font-size: 3rem;
}

.closeButton {
  font-size: 25px;
  color: white;
  width: 150px;
  justify-content: center;
  align-items: center;
  display: flex;
  height: 50px;
  background-color: #f44336;
  border-radius: 10px;
  border: 2px solid rgba(255, 255, 255, 0.062);
  cursor: pointer;
  transition: 0.3s;
}

.closeButton:hover {
  background-color: #ff4949;
  border: 2px solid rgba(255, 184, 184, 0.336);

  box-shadow: 0 0 30px #ff4646a9;
}

.closeButtonText {
  color: white;
  font-size: 18px;
  font-weight: bold;
}